.home-section {
    padding: 100px 20px;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    box-sizing: border-box;
  }
  
  .home-container {
    display: flex;
    width: 100%;
    max-width: 1200px;
    gap: 40px;
    box-sizing: border-box;
  }
  
  /* Adjust left and right column proportions */
  .home-left {
    flex: 2;
  }
  
  .home-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Left Column Styles */
  .home-left h1 {
    font-size: 2.8rem;
    margin-bottom: 20px;
  }
  
  .description {
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 30px;
  }
  
  .info-cards {
    display: flex;
    gap: 20px;
    margin-bottom: 30px;
  }
  
  .card {
    flex: 1;
    background-color: #1e1e1e;
    padding: 15px;
    border-radius: 8px;
  }
  
  .card h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .card ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .card ul li {
    margin-bottom: 8px;
  }
  
  .experience {
    margin-top: 20px;
  }
  
  .experience h2 {
    font-size: 1.8rem;
    margin-bottom: 10px;
  }
  
  .experience ul {
    list-style: none;
    padding: 0;
  }
  
  .experience ul li {
    margin-bottom: 8px;
  }
  
  /* Right Column Styles */
  .photo-wrapper {
    width: 100%;
    max-width: 250px;
    margin-bottom: 20px;
  }
  
  .profile-photo {
    width: 100%;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .technologies {
    width: 100%;
  }
  
  .technologies h2 {
    font-size: 1.5rem;
    margin: 20px 0 10px;
    text-align: center;
  }
  
  .tech-list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 8px;
    font-size: 0.9rem;
  }
  
  .tech-list li {
    background-color: #1e1e1e;
    padding: 6px 10px;
    border-radius: 4px;
    margin-bottom: 5px;
  }
  